import { useEffect, useState } from "react";
import axios from "axios";
import { useRouter } from "next/router";

export default function ChatDetail() {
  const [chat, setChat] = useState(null);
  const router = useRouter();
  const { id } = router.query;

  useEffect(() => {
    if (id) {
      fetchChatDetail();
    }
  }, [id]);

  const fetchChatDetail = async () => {
    try {
      const token = localStorage.getItem("token");
      if (!token) throw new Error("Token tidak ditemukan");

      const response = await axios.get(`http://localhost:3000/api/history/${id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });

      setChat(response.data);
    } catch (error) {
      console.error("Error fetching chat details:", error.response?.data || error.message);
    }
  };

  if (!chat) return <p>Loading...</p>;

  return (
    <div>
      <h1>Detail Chat</h1>
      <p><strong>Title:</strong> {chat.title}</p>
      <p><strong>Content:</strong> {chat.content}</p>
      <p><strong>Created At:</strong> {new Date(chat.createdAt).toLocaleString()}</p>
      <button onClick={() => router.back()}>Kembali</button>
    </div>
  );
}